.todos__single {
    display: flex;
    border-radius: 5px;
    padding: 20px;
    margin-top: 15px;
    background-image: url("https://img.freepik.com/free-photo/crumpled-yellow-paper-background-close-up_60487-2390.jpg?size=626&ext=jpg");
    transition: 0.2s;
}
.todos__single:last-child{
    margin-bottom: 40px;
}
.todos__single:hover {
box-shadow: 0 0 5px black;
transform: scale(1.03);
}

.todos__single--text {
flex: 1;
padding: 5px;
border: none;
font-size: 20px;
}

.todos__single--text:focus {
outline: none;
}

.icon {
margin-left: 10px;
font-size: 25px;
cursor: pointer;
}

