.input{
    display: flex;
    width: 90%;
    position: relative;
    align-items: center;
    margin: 10px auto;
}
.input__box{
    width: 100%;
    padding: 20px 30px;
    border-radius: 50px;
    font-size: 25px;
    border: none;
    transition: .2s;
    box-shadow: inset 0 0 5px #000;
}
.input__box:focus{
    box-shadow: 0 0 10px 1000px rgba(0, 0, 0, 0.5);
    outline: none;
}
.input__submit{
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0px;
    border: none;
    font-size: 15px;
    background-color: #2f74c0;
    color: #fff;
    transition: .2s all;
    box-shadow: 0 0 10px #000;
    cursor: pointer;
}
.input__submit:hover{
    background-color: #388ae2;
}
.input__submit:active{
    transform: scale(.8);
    box-shadow: 0 0 5px #000;
}